import * as React from "react"
import {Container, Divider, Grid, List, ListItem} from "@mui/material";
import { ProjectOverview } from "../components/ProjectOverview";
import { projects } from "../constants";
import Typography from "@mui/material/Typography";
import {useEffect, useState} from "react";
import Box from "@mui/material/Box";


const ProjectsView = () => {

    if (typeof window === `undefined`) {
        return(<></>);
    }
    if(navigator == undefined){
        return
    }

    const mobileScreenWidth = 600

    // @ts-ignore
    const [isMobile, setIsMobile] = useState(false)

    useEffect(() => {
        function handleWindowResize() {
            setIsMobile(window.innerWidth < mobileScreenWidth)
        }
        window.addEventListener('resize', handleWindowResize);
        window.addEventListener('load', handleWindowResize);
        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, []);


  return (
      <Box mt={4}>
          <Container>
              <Typography variant={'h4'}>
                  Projects
              </Typography>
              <Grid container spacing={6}>
                  <Grid item xs={6} justifyContent={"center"}>
                      <ProjectOverview
                          name={projects[0].name}
                          date={projects[0].date}
                          body={projects[0].body}
                          image={projects[0].imageUrl}
                          url={projects[0].url}
                      />
                  </Grid>
                  <Grid item xs={6} justifyContent={"center"}>
                      <ProjectOverview
                          name={projects[1].name}
                          date={projects[1].date}
                          body={projects[1].body}
                          image={projects[1].imageUrl}
                          url={projects[1].url}
                      />
                  </Grid>
                  <Grid item xs={6} justifyContent={"center"}>
                      <ProjectOverview
                          name={projects[2].name}
                          date={projects[2].date}
                          body={projects[2].body}
                          image={projects[2].imageUrl}
                          url={projects[2].url}
                      />
                  </Grid>
                  <Grid item xs={6} justifyContent={"center"}>
                      <ProjectOverview
                          name={projects[3].name}
                          date={projects[3].date}
                          body={projects[3].body}
                          image={projects[3].imageUrl}
                          url={projects[3].url}
                      />
                  </Grid>
              </Grid>
              <Grid container spacing={6}>
                  {projects.map((project) => (
                      <Grid item xs={6} justifyContent={"center"}>
                          <ProjectOverview
                              name={project.name}
                              date={project.date}
                              body={project.body}
                              image={project.imageUrl}
                              url={project.url}
                          />
                      </Grid>
                  ))
                  }
              </Grid>
          </Container>
      </Box>
  );
}

export default ProjectsView
